.Dashboard {
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-columns: 25fr 75fr;
    background-color: #f2f4f8;
}

.Dashboard .Content {
    width: 100%;
    height: 100vh;
    padding-right: 30px;
    padding-top: 70px;
}

.Home {
    width: 100%;
}

.Home .summery {
    width: 100%;
    display: grid;
    grid-template-columns: 25fr 25fr 25fr 25fr;
    grid-column-gap: 20px;
    background-color: #fff;
    font-family: Poppins;
    padding: 20px;
    border-radius: 15px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    margin-bottom: 20px;
}

.Home .summery .Part {
    display: flex;
}

.Home .summery .Part img {
    border-radius: 50%;
}

.Home .summery .Part p {
    color: #72676e;
}

.Home .summery .Part h5 {
    font-weight: bold;
}

.Home .ApplicationIncome {
    display: grid;
    grid-template-columns: 75fr 25fr;
    grid-column-gap: 20px;
}

.Home .ApplicationIncome .Income {
    background-color: #fff;
    font-family: Poppins;
    padding: 20px;
    border-radius: 15px;
    color: #72676e;
}

.Home .ApplicationIncome .Income h3 span {
    font-weight: bold;
    color: #000;
}

.Home .ApplicationIncome .Income .incomeGraph {
    width: 100%;
    min-height: 25vh;
    align-items: flex-end;
    display: grid;
    grid-template-columns: 8.33fr 8.33fr 8.33fr 8.33fr 8.33fr 8.33fr 8.33fr 8.33fr 8.33fr 8.33fr 8.33fr 8.33fr;
    grid-column-gap: 10px;
    color: #72676e;
}

.__react_component_tooltip {
    font-family: Poppins;
}

.Home .ApplicationIncome .Income .incomeGraph .GraphItem {
    width: 100%;
    border-radius: 10px;
    background-color: #e6e9ef;
    cursor: pointer;
    transition: .5s;
}

.Home .ApplicationIncome .Income .incomeGraph .GraphItem:hover {
    background-color: #135bcc;
}

.GraphItemCurrMonth {
    background-color: #135bcc !important;
}

.Home .ApplicationIncome .Income .incomeGraph p {
    margin-bottom: 0;
    font-size: 12px;
    font-family: Quicksand;
    text-align: center;
}

.Home .ApplicationIncome .studentReport {
    width: 100%;
    background-image: linear-gradient( 130deg, #135bcc, #055ffc, #07cbfd );
    border-radius: 15px;
    padding: 20px;
    font-family: Poppins;
    color: #fff;
    font-size: 12px;
}

.Home .ApplicationIncome .studentReport label {
    background-color: #fff;
    color: #135bcc;
    padding: 3px 10px;
    border-radius: 15px;
    font-weight: bold;
}

.Home .ApplicationIncome .studentReport button {
    border: 1px solid #fff;
    background-color: #fff;
    width: 100%;
    border-radius: 10px;
    padding: 8px;
    font-family: Poppins;
    font-weight: bold;
    outline: none;
}

@media only screen and (max-width: 1201px) {
    .Dashboard {
        display: block;
    }

    .Dashboard .Content {
        padding-left: 30px;
        padding-right: 30px;
    }
}

@media only screen and (max-width: 992px) {
    .Home .ApplicationIncome {
        grid-template-columns: 70fr 30fr;
    }

    .Home .ApplicationIncome .studentReport h6 {
        font-size: 15px !important;
    }
    
    .Home .ApplicationIncome .studentReport label,
    .Home .ApplicationIncome .studentReport p {
        font-size: 10px !important;
    }
    
    .Home .ApplicationIncome .studentReport button {
        font-size: 12px;
    }
}

@media only screen and (max-width: 768px) {
    .Dashboard {
        font-size: 12px;
    }

    .Home .summery {
        grid-template-columns: 50fr 50fr;
    }
    
    .Home .ApplicationIncome {
        display: block;
    }
    
    .Home .ApplicationIncome .Income {
        margin-bottom: 20px;
    }
}

@media only screen and (max-width: 600px) {
    .Home .ApplicationIncome .Income {
        overflow: hidden;
    }
    
    .Home .ApplicationIncome .Income .incomeGraph {
        max-width: 100%;
        overflow: auto;
        padding-bottom: 10px;
    }

    .Home .ApplicationIncome {
        padding-bottom: 20px;
    }
}

@media only screen and (max-width: 450px) {
    .Dashboard .Content {
        padding-left: 20px;
        padding-right: 20px;
    }
}